import { Component, OnInit } from "@angular/core";
import { UsersService } from "../../services/users.service";
import { TokenService } from "../../services/token.service";

import io from "socket.io-client";
@Component({
  selector: "app-following",
  templateUrl: "./following.component.html",
  styleUrls: ["./following.component.scss"]
})
export class FollowingComponent implements OnInit {
  socket: any;
  following: any;
  currentUser: any;
  search: any;

  constructor(
    private usersService: UsersService,
    private tokenService: TokenService
  ) {
    this.socket = io("http://localhost:5500");
  }

  ngOnInit() {
    this.currentUser = this.tokenService.getPayload();
    this.getCurrentUser();
    this.socket.on("refreshPage", () => {
      this.getCurrentUser();
    });
  }

  getCurrentUser() {
    this.usersService.getUserById(this.currentUser._id).subscribe(
      data => {
        this.following = data.user.following;
        this.currentUser = data.user;
      },
      err => console.log(err)
    );
  }

  unFollow(user) {
    this.usersService.unFollowUser(user._id).subscribe(res => {
      this.socket.emit("refresh", {});
    });
  }
}
